body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.App{
    width: 800px;
}

.post{
    display: flex;
    padding: 15px;
    border:2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}
.post-enter {
    transform: translateX(-350px);
  }
  .post-enter-active {
    transform: translateX(0px);
    transition: all 500ms ease-in;
  }
  .post-exit {
    opacity: 1;
  }
  .post-exit-active {
   transform: translateX(-350px);
    transition: all 500ms ease-in;
  }
.page__wraper{
  margin-top: 30px;
  display: flex;
}
.page{
  padding: 10px;
  border:1px solid teal;
  cursor: pointer;
}
.page__current{
  border:2px solid tomato;
  font-weight: bold;
}
.navbar{
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background-color: rgb(206, 96, 182);
}

.navbar__links{
  margin-left: auto;
  padding: 30px;
  
}
.links{
  margin-right: 20px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
}

.post__btns{
  display: flex;  
}
